import { lazy } from "react";
import { createBrowserRouter, Navigate } from "react-router-dom";

import ErrorPage from "../pages/error/errorpage";

const App = lazy(() => import(/* webpackChunkName: "App" */ "../App"));
const Home = lazy(() =>
  import(/* webpackChunkName: "Home" */ "../pages/home/index")
);
const Login = lazy(() =>
  import(/* webpackChunkName: "Login" */ "../pages/login/index")
);
const Emtpy = lazy(() =>
  import(/* webpackChunkName: "Empty" */ "../pages/error/empty")
);
const About = lazy(() =>
  import(/* webpackChunkName: "About" */ "../pages/about/index")
);
const FrontEnd = lazy(() =>
  import(/*webpackChunkName: "FrontEnd"*/ "../pages/frontend/index")
);
const Program = lazy(() =>
  import(/*webpackChunkName: "Program"*/ "../pages/computer/index")
);
const Photography = lazy(() =>
  import(/*webpackChunkName: "photography"*/ "../pages/photography/index")
);
const Admin = lazy(() =>
  import(/*webpackChunkName: "Admin"*/ "../pages/admin/index")
);
const Log = lazy(() =>
  import(/*webpackChunkName: "Log"*/ "../pages/admin/logManage/index")
);
const CommentManage = lazy(() =>
  import(
    /*webpackChunkName: "Comment"*/ "../pages/admin/ContentManage/comment/index"
  )
);
const ArtitleManage = lazy(() =>
  import(
    /*webpackChunkName: "Admin"*/ "../pages/admin/ContentManage/artitle/index"
  )
);
const Board = lazy(() =>
  import(/*webpackChunkName: "board"*/ "../pages/admin/dashboard/index")
);

const Redirect = () => <Navigate to="home" />;

const routes = [
  {
    path: "/",
    element: <Redirect />,
  },
  {
    path: "/",
    element: <App />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "home",
        element: <Home />,
        errorElement: <ErrorPage />,
        children: [
          {
            path: "frontend",
            element: <FrontEnd />,
            errorElement: <ErrorPage />,
          },
          {
            path: "program",
            element: <Program />,
            errorElement: <ErrorPage />,
          },
          {
            path: "photography",
            element: <Photography />,
            errorElement: <ErrorPage />,
          },
          {
            path: "about",
            element: <About />,
            errorElement: <ErrorPage />,
          },
        ],
      },
      {
        path: "admin",
        element: <Admin />,
        errorElement: <ErrorPage />,
        children: [
          {
            path: "log",
            element: <Log />,
            errorElement: <ErrorPage />,
          },
          {
            path: "comment",
            element: <CommentManage />,
            errorElement: <ErrorPage />,
          },
          {
            path: "artitle",
            element: <ArtitleManage />,
            errorElement: <ErrorPage />,
          },
          {
            path: "*",
            element: <Emtpy />,
            errorElement: <ErrorPage />,
          },
          {
            path: "board",
            element: <Board />,
            errorElement: <ErrorPage />,
          },
        ],
      },
      {
        path: "/login",
        element: <Login />,
        errorElement: <ErrorPage />,
      },
      {
        path: "*",
        element: <Emtpy />,
        errorElement: <ErrorPage />,
      },
    ],
  },
];

export const setrouter = createBrowserRouter(routes);
